//BACKGROUND

.bg_pattern {
    // padding: 20px;
    margin: 20px;
    font-family: $font-family-FIRST;
    background: linear-gradient(
        121deg,
        rgba(4, 102, 172, 1) 0%,
        rgba(4, 156, 220, 1) 21%,
        rgba(4, 212, 251, 1) 44%,
        rgba(4, 180, 235, 1) 61%,
        rgba(27, 196, 236, 1) 82%,
        rgba(4, 172, 236, 1) 97%,
        rgba(4, 128, 198, 1) 100%
    );
    background: url(/img/bg.svg) no-repeat fixed center;
    
    color: rgb(255, 255, 255);
    animation: fadeIn 1s;

}

.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 3rem;
    // margin-top: 3.6rem;
}
.header__title {
    font-family: $font-family-FIRST;
    font-weight: $font-title-weight;
    color: $font-color-title;
    letter-spacing: $font-title-spacing;
    line-height: 0.6;
    text-transform: uppercase;
    font-size: 2.2rem;
}
.header__description {
    @extend .header__title;
    font-weight: $font-title-sub-weight;
    text-transform: inherit;
    letter-spacing: inherit;
    font-size: 1rem;
}
.button_lamp {
    height: 80%;
    opacity: 0.19;
    position: absolute;
}

.bubble__link {
    color: $font-color-title;
    font-size: 18px;
    text-decoration: none;
    border-radius: 50%;
    text-transform: uppercase;

    font-family: $font-family-FIRST;
    font-weight: $font-title-weight;

    /* for centering background image */

    /* for centering text */
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    line-height: 0.9;
    transition: transform 0.2s;
    &:hover {
        transform: scale(1.1);
    }
    
}

.bubble__why {
    border: 3px solid $the-why-stroke;
    background-color: $the-why-color;

    /* bubble size */
    height: 80px;
    width: 80px;

    /* for absolute positioning */
    position: absolute;
    top: 20%;
    left: 10%;

    //ANIMATION FLOATING
    animation: 4s Infinite floating;
    animation-delay: 0.3s;

}

.bubble__inspired {
    border: 3px solid $get-inspired-stroke;
    background-color: $get-inspired-color;

    /* bubble size */
    height: 100px;
    width: 100px;

    /* for absolute positioning */
    position: absolute;
    top: 20%;
    right: 10%;

    //ANIMATION FLOATING
    animation: 3s Infinite floating;
    animation-delay: 1.2s;

    font-size: 1.1rem;
   
}
.bubble__genie__advice {
    display: none;
    border: 3px solid $share-your-wish-stroke;
    border-radius: 43% 57% 100% 0% / 76% 57% 43% 24%;
    background-color: $wishes-button-color;

    /* bubble size */
    height: 180px;
    width: 180px;

    /* for absolute positioning */
    position: absolute;
    top: 35%;
    left: 26%;
    z-index: 1;

    transition: transform 0.4s;
    &:hover {
        transform: scale(1.1);
        background-color: $share-your-wish-color;
        color: $font-color-title;
    }

    font-size: 1rem;
    // animation: 3s Infinite dream_breath;

    animation: fadeIn 2s;
    // animation-delay: 0.5s;
    // transition: transform 0.4s;

    @keyframes fadeIn {
        0% {
            opacity: 0;
        }
        100% {
            opacity: 1;
        }
    }
}

.bubble__wish {
    border: 3px solid $share-your-wish-stroke;
    background-color: $share-your-wish-color;

    /* bubble size */
    height: 180px;
    width: 180px;

    /* for absolute positioning */
    position: absolute;
    top: 40%;
    left: 26%;
    z-index: 1;
   
    font-size: 2.3rem;

    animation: 3s Infinite dream_breath;
    
}
.bubble__dreams {
    border: 3px solid $people-dream-stroke;
    background-color: $people-dream-color;

    /* bubble size */
    height: 120px;
    width: 120px;

    //ANIMATION FLOATING
    animation: 3s Infinite floating;
    animation-delay: 1s;

    /* for absolute positioning */
    position: absolute;
    bottom: 18%;
    right: 10%;
    
    animation-name: dream_breath;
    animation-duration: 4s;

    @keyframes dream_breath {
        // from {background-color: red;}
        0% {
            transform: scale(1);
        }
        25% {
            transform: scale(1.05);
        }
        50% {
            transform: scale(1.08);
        }
        100% {
            transform: scale(1);
        }
    }
}

