.error__form__text {
    @extend .dreams__title;
    line-height: 1;
    color: $share-your-wish-color;
    //To make a border text, the use of shadows.
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

.inactive {
    font-size: 25px;
    display: none;
}

.no_underline {
    text-decoration: none;
}

///DOWNLOAD DREAM
/// 
.fade {
    animation: fadeIn 0.5s;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

.card__box__generic {
    
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    border-radius: 10px;
    border: 3px solid $the-why-stroke;

    padding: 1rem;

    max-width: 400px;
    height: auto;
    text-align: center;

    animation: card_shado_move 5s infinite;
    

    @keyframes card_shado_move{
        50% {     box-shadow: 1px 1px 0 rgba(255, 255, 255, 0.1215686275), -1px 0 28px 0 #0dcaf0, 28px 28px 28px 0 rgba(13, 202, 240, 0.0901960784);
        }
    }
    

}

.card__box__dreams {
    background-color: #FBAB7E;
    background-image: linear-gradient(62deg, #FBAB7E 0%, #F7CE68 100%);

   
    animation-delay: 0s, 0.9;

}
.card__box__goals {
    background-color: #1f87a1;
    background-image: linear-gradient(90deg, #1f87a1 0%, #1bc4ec 100%);

}
.card__box__wishes {
    background-color: #0093E9;
    background-image: linear-gradient(160deg, #0093E9 0%, #80D0C7 100%);

}
.card__box__ideas {
    background-color: #FAD961;
    background-image: linear-gradient(90deg, #FAD961 0%, #dd7973 100%);

}

.logo__dreamcloud {
    text-align: center;
}
.logo__title {
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: 800;
    line-height: 0.1;
    letter-spacing: 2px;
}
.logo__subtitle {
    font-size: 0.5rem;
    font-weight: 700;
}

.card__box__dream {
    text-align: center;
    margin-top: 1rem;
    font-size: 1rem;
}
.card__box__author {
    text-transform: uppercase;
    text-align: center;
    font-size: 0.95rem;
}
.card__box__date {
    font-size: 0.9rem;
    text-align: center;
}
.card__container {
    display: flex;
    justify-content: center;
    align-items: center;
}
.card__box__type {
    background-color: $share-your-wish-color;
    color: $font-color-title;

    border: none;
    border-radius: 10px;
    //margin-right: 7px;
    width: 100%;
    margin-bottom: 1rem;
    //TRYOUT
    

    text-transform: uppercase;
    font-weight: $font-title-weight;
}
.card__box__img__responsive{
    
       
        display: block;
        margin-left: auto;
        margin-right: auto;

         // width: 100%;
    max-width: 300px;
    height: auto;
    //margin-top: 1rem;
    border-radius: 91% 9% 94% 6%/9% 96% 4% 91%;
    transition: transform 0.2s;

    box-shadow: $box-shadow;

    &:hover {
        transform: scale(1.03);
    }
}

.language-button{
    background: none;
    border: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
}

.language-selection{
    display: flex;
    justify-content: center;
}


.card__button__back {
    background-color: $people-dream-color;
    color: $home-button-color;
    border: none;
    border-radius: 10px;
    margin-right: 7px;
    text-transform: uppercase;
    font-weight: 800;
    text-decoration: none;
    padding: 0.5rem;
}
.soon__text {
    text-transform: uppercase;
    text-align: center;
    margin: 1rem;
    font-weight: $font-title-weight;
}

.photo_output {
    display: flex;
    justify-content: center;
    align-items: center;
    //height: 100vh; /* Opcional: ajusta segn el diseo */
    
    overflow: hidden; /* Evita barras de desplazamiento */
}

.photo_output canvas {
    width: 100%; /* Asegura que el canvas no exceda el tamao del contenedor */
    height: 100%; /* Asegura que el canvas no exceda el tamao del contenedor */
    display: flex;
    justify-content: center;
    align-items: center;
}

